<template>
  <div class="container collection-box">
	<div class="avatar-list flex-middle">
	  <div class="item" v-for="item in avatars">
		<div class="svg" :class="'svg' + item.tokenId + ' svg' + item.tokenId % 3" @click="changeImg(item.tokenId)">
			<img style="display: block; width: 100%" :src="item.svg.image_data" alt="">
		</div>
		<div class="title">Dank-punk  #{{item.tokenId.length < 5 ? '0'.repeat(5-item.tokenId.length) + item.tokenId: item.tokenId}}</div>
		<div class="avatar-btn-box flex-middle">
          <div class="avatar-btn flex-item" v-if="avatar != item.svg" @click="setAvatar(item.svg)">Set Avatar</div>
		  <div class="avatar-btn flex-item" v-else>Selected</div>
		  <a :href="getHerf(item.tokenId)"><img src="@/assets/images/share.png" alt=""></a>
		</div>
		
	  </div>
	</div>
   <div class="page-box">
	 	 <el-pagination
		background
		layout="prev, pager, next"
		:page-size="limit"
		:current-page="page"
		@current-change="currentChange"
		:total="total">
		</el-pagination>
   </div>
  </div>
</template>
<script>
import { reactive, toRefs, computed, watch} from "vue";
import web3Data from "@/utils/initWeb3";
import {useStore} from "vuex";
import config from "@/utils/config";
import nftAbi from "@/utils/nftAbi";
import { saveSvgAsPng }  from 'save-svg-as-png';
import { ElMessage, ElLoading } from 'element-plus'

export default {
  setup () {
     const store = useStore();
     const data = reactive({
       web3: computed(() => store.state.provider),
	   rate: computed(() => store.state.rate),
       address: computed(() => store.state.accounts[0]),
	   avatar: computed(() => store.state.avatar),
	   currentBalance: computed(() => store.state.currentBalance),
	   nftContract: null,
	   total: 0,
	   limit: 4,
	   page: 1,
	   avatars: [],
	   initContract: () => {
		 data.nftContract = new data.web3.eth.Contract(nftAbi, config.nftAddress)
		 data.initData()
	   },
	   currentChange: (page) => {
         console.log(page)
		 data.page = page
		 data.getAvatars()
	   },
	   initData: () => {
		 data.getAvatars()
	   },
	   initWeb3: () => {
         web3Data.initWeb3()
       },
	   changeImg: (id) => {
		let svg = document.querySelector(`.svg${id} svg` );
		console.log(svg)
		saveSvgAsPng(svg, 'my_svg');
	   },
	   getHerf: (tokenId) => {
		 return `https://twitter.com/intent/tweet?text=${window.location.origin}/#/svg/${tokenId}`
	   },
	   setAvatar: (svg) => {
		 ElMessage.success('Set Success')
		 store.commit('updateAvatar', JSON.stringify(svg))
	   },
	   getAvatars: async () => {
		 let arr = []
		let loadingInstance = ElLoading.service({lock: true,
          text: 'Please wait while the data is loaded',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'});
         let tokens = await data.nftContract.methods.getUserAllTokens(data.address).call()
		 data.total = tokens.length
		 let num = tokens.length - data.page * data.limit
		 num = num > 0 ? num : 0
		 for(let i = tokens.length - (data.page - 1) * data.limit - 1; i >= num; i--) {
		   let svg = await data.nftContract.methods.tokenURI(tokens[i]).call()
		   let params = {
			 tokenId: tokens[i],
			 svg: JSON.parse(svg)
		   }
		   arr.push(params)
		 }
		 data.avatars = arr
		 loadingInstance.close();
	   }
     })
     if (data.address) {
		 data.initContract()
     }
     watch(() => data.address, (val) => {
       if (val) {
		data.initContract()
       }
     })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="scss">
.avatar-list{
 svg{
  width: 100% !important;
  max-height: 280px !important;
 }
}
.el-pagination.is-background .btn-next.disabled, .el-pagination.is-background .btn-next:disabled, .el-pagination.is-background .btn-prev.disabled, .el-pagination.is-background .btn-prev:disabled, .el-pagination.is-background .el-pager li.disabled{
  color: #fff !important;
  background: rgba($color: #fff, $alpha: 0.2) !important;
}
.el-pagination.is-background .btn-next, .el-pagination.is-background .btn-prev, .el-pagination.is-background .el-pager li{
  background: none !important;
  color: #fff!important;
}
.el-pagination.is-background .btn-next, .el-pagination.is-background .btn-prev{
   background: #20CAA2 !important;	
}
.el-pagination.is-background .el-pager li:not(.disabled).active{
	color: #20CAA2!important;
}
</style>
<style lang="scss" scoped>
.container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
}
.collection-box{
  padding: 120px 0 100px;
  .avatar-list{
	 flex-wrap: wrap;
	 min-height: 40vh;
	.item{
	  width: 19%;
	  margin: 0 3% 40px;
	  .svg{
	    border-radius: 8px;
		&.svg0 {
		  background: url('~@/assets/images/bg1.png') no-repeat center center;
		  background-size: 100% 100%;
		}
		&.svg1 {
		  background: url('~@/assets/images/bg2.png') no-repeat center center;
		  background-size: 100% 100%;
		}
		&.svg2 {
		  background: url('~@/assets/images/bg3.png') no-repeat center center;
		  background-size: 100% 100%;
		}
		// &:nth-of-type(3n+2) {
		//   background: url('~@/assets/images/bg2.png') no-repeat center center;
		//   background-size: 100% 100%;
		// }
		svg{
		  width: 100% !important;
		  height: auto !important;
		}
	  }
	  .title{
		 color: #20CAA2;
		 font-size: 16px;
		 margin-top: 15px;
		 padding: 0 12px;
	  }
	  .avatar-btn-box{
        margin: 25px auto 0;
		a{
		  margin-left: 20px;
		  img
		  {
			  height: 40px;
		  }
		}
	  }
	  .avatar-btn{
		cursor: pointer;
		height: 40px;
		box-sizing: border-box;
		
		border-radius: 20px;
		width: 90%;
		text-align: center;
		line-height: 38px;
		border: 1px solid #20CAA2;
		font-size: 16px;
		color: #20CAA2;
	  }
	}
  }
}
.page-box{
  display: flex;
  justify-content: center;
}
@media screen and (max-width: 480px) {
  .collection-box{
  padding: 120px 0 100px;
  .avatar-list{
	 flex-wrap: wrap;
	.item{
	  width: 46%;
	  margin: 0 2% 30px;
	  .svg{
		 background: #20CAA2;
	  border-radius: 8px;
		svg{
		  width: 172px !important;
		  height: 172px !important;
		}
	  }
	   .avatar-btn-box{
        margin: 25px auto 0;
		a{
		  margin-left: 20px;
		  img
		  {
			  height: 40px;
		  }
		}
	  }
	  .avatar-btn{
		cursor: pointer;
		height: 40px;
		box-sizing: border-box;
		border-radius: 20px;
		width: 90%;
		text-align: center;
		line-height: 38px;
		border: 1px solid #20CAA2;
		font-size: 16px;
		color: #20CAA2;
	  }
	}
  }
}	
}
</style>
